import React from "react";
import { Tag as AntTag, Form } from "antd";
import { CustomSchema } from "./Form/types";

export default function TagPreview<T>(props: CustomSchema<T>) {
  return (
    <Form.Item {...props} shouldUpdate labelCol={{ span: 0 }} wrapperCol={{ span: 24 }}>
      {({ getFieldValue }) => (
        <AntTag
          style={{ width: "100%", height: 28, textAlign: "center" }}
          color={getFieldValue("color")?.hex}
        >
          {getFieldValue("name") || "Demo"}
        </AntTag>
      )}
    </Form.Item>
  );
}
